.bread-header {
    background: #394578 !important;
    height: 40px !important;
    width: 100% !important;
    color: #fff !important;
    border-radius: 0 !important;
}

.bread-header-list {
    font-size: 12px !important;
    width: 100% !important;
}

.imageCombo{
    width: 100%;
    height: 129px;
}

.imageComboCheckout{
    width: 100%;
    height: 129px;
}

