    }

    .imageComboCheckout{
        height: 100px;
    }
}

@media only screen and (min-width:350px) and (max-width:378px){
    .imageCombo{
        height: 80px;
    }

    .imageComboCheckout{
        height: 80px;
    }
}

@media only screen and (min-width:320px) and (max-width:340px){
    .imageCombo{
        height: 70px;
    }

    .imageComboCheckout{
        height: 70px;
    }
}

