.cartTitle {
    margin: 0px 0px 10px;
    display:flex;
    justify-content:center;
    font-size: 18px;
    font-family: 'Playfair Display', serif !important;
    letter-spacing: 0.20em !important;
    color: #6D6E71;
    align-items: center;
    margin-top: 10px;
    
}

@media only screen and (min-width:320px) and (max-width:425px) {
    .cartTitle{
      font-size: 14px;
    }
    .cardBorderMobile{
        width:50%;
        margin-left: 100px !important;
        margin-top: -20px;
    }
  }


.cardBorderMobile {
    border: 1px solid #6D6E71 !important;
}

.total{
    color: #6D6E71 !important;
    font-weight: 700 !important
}

.bread-header ol li a:after {
    content: " ";
    display: block;
    width: 0;
    height: 0;
    border-top: 35px solid transparent;
    border-bottom: 25px solid transparent;
    border-left: 30px solid #394578;
    position: absolute;
    top: 50% !important;
    margin-top: -40px;
    left: 100%;
    z-index: 2;
}

@media screen and (max-width: 1220px) {

    .ch-d-vl {
        overflow: hidden;
        text-overflow: ellipsis;
        color: #777 !important;
        font-size: 13px !important;
        font-weight: 100 !important;
    }
}

@media screen and (min-width: 1220px) {

    .ch-d-vl {
        position: relative;
        top: 5px;
        left: 10px;
        text-align: center;
        text-decoration: none;
        overflow: hidden;
        text-overflow: ellipsis;
        display: inline-block;
        color: #777 !important;
        font-size: 13px !important;
        font-weight: 100 !important;
    }

}

.bread-header ol li a:before {
    content: " ";
    display: block;
    width: 0;
    height: 0;
    border-top: 35px solid transparent;
    border-bottom: 25px solid transparent;
    border-left: 30px solid white;
    position: absolute;
    top: 50%;
    margin-top: -40px;
    margin-left: 1px !important;
    left: 100%;
    z-index: 1;
}

.bread-header ol li a {
    color: #FFF;
    text-decoration: none;
    padding: 12px 0 10px 35px;
    background: #394578;
    position: relative;
    display: block;
    float: left;
    font-size: 11px;
}

.product-image-thumb {
    /* margin-top: 10px !important; */
    text-align: center;
    width: 100% !important;
    margin: 0px auto !important;
    border: 0.5px solid rgba(0, 0, 0, 0.12);
    padding: 15px 0px;
    /* box-shadow: 10px 10px 16px 4px #dedada !important; */
    box-shadow: none !important;
    border-radius: none !important;
    outline: none !important;
}
.product-image-thumb-check {
    /* margin-top: 10px !important; */
    text-align: center;
    width: 100% !important;
    height: 90% !important;
    margin: 0px auto !important;
    border: 0.5px solid rgba(0, 0, 0, 0.12);
    padding: 10px 0px;
    /* box-shadow: 10px 10px 16px 4px #dedada !important; */
    box-shadow: none !important;
    border-radius: none !important;
    outline: none !important;
}

.image {
    width: 100% !important;
    height: 100% !important;
}

.title-cart {
    font-weight: 500 !important;
    height: 15px !important;
    color: #6D6E71 !important;
    font-size: 14px !important;
     margin-top: 0;
     margin-bottom: 10px;
}

.title-response,
.MuiCardHeader-root {
    color: #394578;
    line-height: 1.5em;
    height: 2em;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    width: 92%;
    font-size: 15px !important;
    padding: 0 6px !important;
}

.product-rate {
    float: left;
    margin-left: 0;
}

.offer-price,
.text-hint {
    text-decoration: line-through;
    font-size: 13px;
    margin-bottom: 2px;
    color: #828282;
}

.price {
    font-size: 25px;
    margin-bottom: 2px;
}

.price-response {
    padding: 0px 10px;
    font-size: 13px !important;
    color: #ed1165;
    padding-bottom: 25px;
    padding-top: 10px;
}

.offer-description {
    margin-bottom: 2px;
    color: #FFF;
    border-radius: 3px !important;
    padding: 1px 5px !important;
    font-weight: 400 !important;
    font-size: 12px !important;
}

.btn-cart-resp:hover {
    background: #f36e23 !important
}

.remove-product {
    top: 8px !important;
    width: 15px;
    height: 15px;
    background: url(https://assets.stylori.com/images/static/icon-close.png) no-repeat;
    position: relative;
}

.subhesder {
    padding: 4px 3px !important;
    font-size: 14px !important;
    color: #6D6E71 !important;
    width: 100% !important;
    overflow: hidden;
}


.totsl-size {
    font-size: 18px !important;
    margin-top: 10px !important;
    margin-bottom: 10px !important;
    padding: 2px !important;
}

.subhesder-response {
    padding: 4px 3px !important;
    font-size: 11px !important;
    color: #394578;
    width: 100% !important;
}

.checkout-response-card {
    box-shadow: 0px 0px 28px -9px rgba(0, 0, 0, 0.77) !important;
    margin-bottom: 20px;
    padding-left: 10px !important;
    height: 230px !important;
    overflow-y: auto !important;
    width: 95% !important;
}

.response-image {
    padding: 0px !important;
    margin: 0px !important;
